import React, { useState, useCallback } from 'react';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import Divider from '@mui/material/Divider';
import TextField from '@mui/material/TextField';
import { IFrameReactContainer, useTranslation } from '@fanfanlo';
import { IMarketConf, IUrlOrHtmlStringConf } from './add-market-by-url.intf';
import MarketInputForm from './market-input-form/MarketInputForm';

const AddMarketByUrl: React.FC = () => {
  const [name, setName] = useState<string>('');
  const [summary, setSummary] = useState<string>('');
  const [description, setDescription] = useState<string>('');
  const [urlOrHtmlConf, setUrlOrHtmlConf] = useState<IUrlOrHtmlStringConf | null>(null);
  const [fullMarketConf, setFullMarketConf] = useState<IUrlOrHtmlStringConf | null>(null);

  const {t} = useTranslation("homepage/components/page/add-market-by-url/ui/content");
  const iframeStyle: React.CSSProperties = {
    minWidth: '300px',
    minHeight: '200px',
    border: '1px solid #ccc',
    display: 'block',
  };

  const handleMarketConfigReady = useCallback((config: IUrlOrHtmlStringConf) => {
    setUrlOrHtmlConf(config);
  }, []);

  const handleFullMarketConfigReady = useCallback((config: IUrlOrHtmlStringConf) => {
    setFullMarketConf(config);
  }, []);

  const itemMarket = urlOrHtmlConf;

  return (
    <Card sx={{ maxWidth: 800, margin: 'auto', mt: 2 }}>
      <CardContent>
        <Box sx={{ mb: 2, color: 'error.main' }}>
          {t("content.warning")}
        </Box>
        <Typography variant="h5" component="h2" gutterBottom>
          {t("content.setNewMarket")}
        </Typography>

        <Box sx={{ mb: 2 }}>
          <TextField
            fullWidth
            label={t("content.marketName")}
            variant="outlined"
            value={name}
            onChange={(e) => setName(e.target.value)}
            sx={{ mb: 1 }}
          />
          <TextField
            fullWidth
            label={t("content.marketSummary")}
            variant="outlined"
            value={summary}
            onChange={(e) => setSummary(e.target.value)}
            multiline
            rows={2}
            sx={{ mb: 1 }}
          />
          <TextField
            fullWidth
            label={t("content.marketDescription")}
            variant="outlined"
            value={description}
            onChange={(e) => setDescription(e.target.value)}
            multiline
            rows={4}
            sx={{ mb: 2 }}
          />
        </Box>

        <Typography variant="h6" gutterBottom sx={{ mt: 2 }}>
          {t("content.itemMarket")}
        </Typography>
        <MarketInputForm onMarketConfigReady={handleMarketConfigReady} isFullMarketPage={false} />

        <Typography variant="h6" gutterBottom sx={{ mt: 2 }}>
          {t("content.fullMarket")}
        </Typography>
        <MarketInputForm onMarketConfigReady={handleFullMarketConfigReady} isFullMarketPage={true} />

        <Divider sx={{ my: 2 }} />

        {(name || summary || description || itemMarket || fullMarketConf) && (
          <Box>
            <hr />
            
            <Typography sx={{textAlign: 'center'}} variant="h6" component="h3" gutterBottom>
            {t("content.settingPreview")}
              
            </Typography>
            <Typography variant="body1" component="p" paragraph>
              <strong>{t("content.marketName")}:</strong> {name || t("content.isNotSet")}
            </Typography>
            <Typography variant="body1" component="p" paragraph>
              <strong>{t("content.marketSummary")}:</strong> {summary || t("content.isNotSet")}
            </Typography>
            <Typography variant="body1" component="p" paragraph>
              <strong>{t("content.marketDescription")}:</strong> {description || t("content.isNotSet")}
            </Typography>

            {itemMarket && (
              <Box sx={{ mb: 2 }}>
                <Typography variant="subtitle1" gutterBottom>
                  {t("content.marketCardPreview")}
                </Typography>
                <IFrameReactContainer key={`item-market-${itemMarket.str}`}
                  url={itemMarket.isUrl ? itemMarket.str : undefined}
                  content={!itemMarket.isUrl ? itemMarket.str : undefined}
                  style={iframeStyle}
                />
              </Box>
            )}

            {fullMarketConf && (
              <Box>
                <Typography variant="subtitle1" gutterBottom>
                  {t("content.marketFullPreview")}  
                </Typography>
                <IFrameReactContainer key={`full-market-${fullMarketConf.str}`}
                  url={fullMarketConf.isUrl ? fullMarketConf.str : undefined}
                  content={!fullMarketConf.isUrl ? fullMarketConf.str : undefined}
                  style={iframeStyle}
                />
              </Box>
            )}
          </Box>
        )}
      </CardContent>
    </Card>
  );
};

export default AddMarketByUrl;
