import React, { useState, useCallback } from 'react';
import TextField from '@mui/material/TextField';
import Checkbox from '@mui/material/Checkbox';
import FormControlLabel from '@mui/material/FormControlLabel';
import Box from '@mui/material/Box';
import { useDebounceFn } from 'ahooks';
import { useTranslation } from '@fanfanlo';
import axios from 'axios';

interface MarketInputFormProps {
  // Callback to notify parent when valid input is ready
  onMarketConfigReady: (config: { str: string; isUrl: boolean }) => void;
  // Initial values if needed
  initialValue?: string;
  initialIsUrl?: boolean;
  isFullMarketPage:boolean;
}

async function loadHtmlFromUrl(value: string): Promise<string> {
  const url = `https://autumn-mouse-8da2.mailregios.workers.dev/?url=${encodeURIComponent(value)}`
  const res = await axios.get(url);
  if(!res.data || !res.data.html){
    throw new Error('Failed to fetch HTML content.')
  }
  // 这个html就是html的内容。
    // 获取url的origin
  const { origin } = new URL(value);
  // 替换所有script和link标签的src/href为绝对路径
  let html = res.data.html;
  // 拼接时去除重复斜杠，保证只有一个/
  function joinOriginPath(origin: string, path: string) {
    let url: string;
    if (origin.endsWith('/') && path.startsWith('/')) {
      url = origin + path.slice(1);
    } else if (!origin.endsWith('/') && !path.startsWith('/')) {
      url = origin + '/' + path;
    } else {
      url = origin + path;
    }
    // 追加__time__参数
    const timeParam = `__time__=${Date.now()}`;
    if (url.includes('?')) {
      // 已有参数，判断是否已带__time__，避免重复
      if (/([&?])__time__=/.test(url)) return url;
      return url + (url.endsWith('?') || url.endsWith('&') ? '' : '&') + timeParam;
    } else {
      return url + '?' + timeParam;
    }
  }
  // 替换<script src="...">
  html = html.replace(/(<script[^>]*src=["'])(?!https?:|\/\/)([^"'>]+)/gi, (match:any, p1:any, p2:any) => `${p1}${joinOriginPath(origin, p2)}`);
  // 替换<link href="...">，常用于css
  html = html.replace(/(<link[^>]*href=["'])(?!https?:|\/\/)([^"'>]+)/gi, (match:any, p1:any, p2:any) => `${p1}${joinOriginPath(origin, p2)}`);
  // 替换<style>@import '...'</style> 里的路径（简单处理）
  html = html.replace(/(@import\s+["'])(?!https?:|\/\/)([^"']+)/gi, (match:any, p1:any, p2:any) => `${p1}${joinOriginPath(origin, p2)}`);
  console.log('parsed html=', html)
  return html;
}

const MarketInputForm: React.FC<MarketInputFormProps> = ({ 
  isFullMarketPage,
  onMarketConfigReady,
  initialValue = '',
  initialIsUrl = true, // Default to URL input
}) => {
  const [inputValue, setInputValue] = useState<string>(initialValue);
  const [isUrl, setIsUrl] = useState<boolean>(initialIsUrl);
  const [error, setError] = useState<string>('');
  const {t} = useTranslation("homepage/components/page/add-market-by-url/ui/market-input-form/content");
  // Basic URL validation (can be improved)
  const isValidUrl = (url: string): boolean => {
    try {
      new URL(url);
      return true;
    } catch (_) {
      return false;
    }
  };

  // The core logic for processing input
  const processInput = useCallback(async (value: string, isUrlChecked: boolean) => {
    console.log('Processing input:', value, 'Is URL:', isUrlChecked);
    if(!value){
      setError('')
      onMarketConfigReady({ str: '', isUrl });
      console.log('cleared')
      return;
    }
    if (isUrlChecked) {
      if (value && isValidUrl(value)) {
        const html = await loadHtmlFromUrl(value)
        if(html){
          setError('');
          onMarketConfigReady({ str: html, isUrl: false });
        }else{
          setError('Failed to fetch HTML content.');
        }
      } else if (value) {
        setError('Please enter a valid URL.');
      } else {
        setError(''); // Clear error if input is empty
      }
    } else {
      // For HTML content, maybe trigger on button click or explicit action?
      // For now, let's assume direct content is always 'ready'
      setError(''); 
      onMarketConfigReady({ str: value, isUrl: false });
    }
  }, [onMarketConfigReady]);

  // Create the debounced version of the processing function
  const { run: debouncedProcessInput } = useDebounceFn(
    processInput,
    {
      wait: 500, // Debounce wait time in milliseconds (e.g., 500ms)
    },
  );

  const handleInputChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const newValue = event.target.value;
    setInputValue(newValue);
    // Call the debounced function when typing
    debouncedProcessInput(newValue, isUrl);
  };

  const handleCheckboxChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const newIsUrl = event.target.checked;
    setIsUrl(newIsUrl);
    // Call the original function immediately when checkbox changes
    processInput(inputValue, newIsUrl); 
  };
  let isUrlLabel = "";
  if(isUrl){
    isUrlLabel = isFullMarketPage ? t('content.full.url') : t('content.item.url');
  }else{
    isUrlLabel = isFullMarketPage ? t('content.full.html') : t('content.item.html');
  }
  let placeholder = ""
  if(!isUrl){
    placeholder = isFullMarketPage ? `<html><body><div>script list<div></body></html>` :`<html><body><div>${t('content.item.come')}<a href="https://github.com/your-name/your-repo/docs/index.html">${t('content.item.myMarketName')}</a>${t('content.item.stroll')}</div></body></html>`
  }else{
    placeholder = isFullMarketPage ? `https://github.com/your-name/your-repo/docs/market.html` : `https://github.com/your-name/your-repo/docs/summary.html`
  }
  return (
    <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2 }}>
      <TextField
        label={isUrlLabel}
        variant="outlined"
        fullWidth
        value={inputValue}
        onChange={handleInputChange}
        error={!!error}
        helperText={error}
        placeholder={placeholder}
        multiline={!isUrl} // Allow multiline for HTML
        rows={isUrl ? 1 : 4} // Adjust rows based on type
      />
      <FormControlLabel
        control={
          <Checkbox 
            checked={isUrl} 
            onChange={handleCheckboxChange} 
          />
        }
        label={t("content.isUrl")}
      />
    </Box>
  );
};

export default MarketInputForm;
